.flash-container{
    position: fixed;
    right: 0;
    height: 20px;
}

.alert {
    opacity: 0;
    left: 100px;
  
    transition: all 700ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  
    &.active {
      opacity: 1;
      left: 0px;
  
      &.exiting {
        opacity: 0;
        left: 100px;
      }
    }
  }